.navigator {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: #111;
    border-radius: 12px;
    padding: 6px;
    overflow: hidden;
}

.navItem {
    position: relative;
    flex: 1;
    text-align: center;
    padding: 10px 12px;
    cursor: pointer;
    font-weight: 500;
    color: #aaa;
    z-index: 2;
    transition: color 0.3s, text-shadow 0.3s;
}

.active {
    color: #00ff1e;
    text-shadow: 0 0 8px rgba(0, 255, 42, 0.8);
}

.navHighlight {
    position: absolute;
    top: 4px;
    bottom: 4px;
    width: calc(100% / 2 - 8px);
    /* 3 вкладки → ширина 1/3 */
    border-radius: 20px;
    background: rgba(103, 103, 103, 0.15);
    transition: transform 0.7s ease;
    z-index: 1;
}

/* Позиции овала */
.navigator.tab-0 .navHighlight {
    transform: translateX(0%);
}

.navigator.tab-1 .navHighlight {
    transform: translateX(103%);
}