.blok {
    margin: 20px 10px;
    background: #151515;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    opacity: 0;
    transform: translateY(20px);
    animation: slideFadeIn 0.6s ease forwards;
}

.blok:nth-child(1) {
    animation-delay: 0.1s;
}
.blok:nth-child(2) {
    animation-delay: 0.2s;
}
.blok:nth-child(3) {
    animation-delay: 0.3s;
}
.blok:nth-child(4) {
    animation-delay: 0.4s;
}
.blok:nth-child(5) {
    animation-delay: 0.5s;
}
.blok:nth-child(6) {
    animation-delay: 0.6s;
}
.blok:nth-child(6) {
    animation-delay: 0.7s;
}
.blok:nth-child(7) {
    animation-delay: 0.8s;
}
.blok:nth-child(8) {
    animation-delay: 0.9s;
}
.blok:nth-child(9) {
    animation-delay: 1.0s;
}

@keyframes slideFadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.title {
    font-weight: 600;
    font-size: 17px;
}

.pts {
    margin-top: 10px;
    font-size: 15px;
    color: #00ff1e;
    text-shadow: 0 0 8px rgba(0, 255, 42, 0.8);
}

.img {
    width: 50px;
    margin-left: 10px;
}
